$(function () {

    /* tooltips */
    $('[data-toggle="tooltip"]').tooltip();

    /* select2 multi */
    $(".multi-select2").select2({
        allowClear: true,
        tags: [],
        tokenSeparators: [",", " "],

    });
    /* select2 one select */
    $(".one-select2").select2();

    /* Bootstrap Switch checkbox*/
    $(".switch-checkbox").bootstrapSwitch();

    /* summernote text-editor*/
    $('.summernote').summernote({
        height: 300,
        minHeight: 200,
        maxHeight: 800,
        placeholder: 'Введите текст',
    });
    /* input file */
    bsCustomFileInput.init();

})


/**
 * Flash-уведомление
 * @param msg
 * @param title
 */
function flash(msg, title = 'Уведомление') {
    $(document).Toasts('create', {
        title: title,
        body: msg,
        class: 'm-5',
        position: 'bottomRight',
        autohide: true,
        delay: 5000,
    });
}


function ajaxDelete($this, parentElement = '.image-wrap') {
    $.ajax({
        type: 'post',
        url: $this.data('url'),
        headers: {
            'X-CSRF-Token': $('meta[name="csrf-token"]').attr('content')
        },
        data: {id: $this.data('id')},
        success: function (data) {
            console.log($this.parents(parentElement));
            $this.parents(parentElement).slideUp(300);
            setTimeout(function () {
                $this.parents(parentElement).remove();
                flash(11111);
            }, 300);
        },
        error: function () {
            alert('Не удалось удалить');
        }
    });
}